.container {
  width: 80%;
  margin: 0 auto;
}

.main-content {

  background:rgba(138,109,33,0.16);
  border-top: 5px solid #8A6D21;

}

.flex {
  display: flex;
  flex-wrap: wrap;
}


/* Media queries */

@media (max-width:700px) {
  .flex {
    flex-direction: column;
  }

  .flex-categories {
    display: flex;
    flex-wrap: wrap;
    width: 100%;
  }
}