@import url(https://fonts.googleapis.com/css2?family=PT+Sans:wght@400;700&family=Russo+One&display=swap);
* {
  margin: 0;
  padding: 0;
}

body {
  font-family: "PT Sans", sans-serif;
}
.container {
  width: 80%;
  margin: 0 auto;
}

.main-content {

  background:rgba(138,109,33,0.16);
  border-top: 5px solid #8A6D21;

}

.flex {
  display: flex;
  flex-wrap: wrap;
}


/* Media queries */

@media (max-width:700px) {
  .flex {
    flex-direction: column;
  }

  .flex-categories {
    display: flex;
    flex-wrap: wrap;
    width: 100%;
  }
}
header {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 1rem 0;
  border-bottom: 5px solid #8A6D21;
  color: #8A6D21;
  background: rgba(138,109,33,0.16);
}
.norris {
  width: 100px;
  padding: 0;
  margin: 0;
}

.heading {
  margin-bottom: 1rem;
  font-family: "Russo One";
  font-weight: 300;

}
.quotedJoke {
  padding: 3rem 0;
  text-align: left;
  display: flex;
  margin-left: 3em;
  width: 70%;
  margin: 0 auto;
}

.quotedJoke .joke {
  font-size: 1.5rem;
  color: #8A6D21;
  
}

.quotation {
  height: 5rem;
  width: 5rem;
  background-image: url(data:image/png;base64,);
  background-position: top left;
  background-repeat: no-repeat;
  background-size: 75%;
  
}
.category {
  padding: .5em;
  text-transform: capitalize;
  cursor: pointer;
  border-bottom: 2px solid #8A6D21;
  color: #8A6D21;
}

.category:hover,
.category:active,
.category:focus {
  background: #8A6D21;
  color: #fff;
}

.active {
  background: #8A6D21;
  color: #fff;
}
.jokes {
  text-align: center;
  font-size: 1.5rem;
  font-weight: bolder;
  margin: 2rem;
  padding: 3rem;
  color: #8A6D21;
  background: rgba(138,109,33,0.2);
  border: 2px solid #8A6D21;
  height: 100%;
  width: 300px;
  background-image: url(/chuck-a-joke/static/media/Joy.93a45085.png);
  background-position: 50% 5px;
  background-repeat: no-repeat;
  background-size: 10%;
}

/* Media queries */
@media (max-width:700px) {
  .jokes {
    width: 190px;
    font-size: .9rem;

  }

}
* {
  margin: 0;
}

footer {
  padding: 1rem 0;
  text-align: center;
  background-color: #8A6D21;
  color: #fff;
}


a {
  display: inline-block;
  color: #fff;
  text-decoration: none;
  border-bottom: 1px solid #fff;
  padding: .2rem;
}

/* Media queries */
@media (max-width:700px) {
  .footer {
    width: 100%;

  }
}
